*{
    margin: 0px;
    padding: 0px;}


#Form-container{
    width: 100%;
    display: flex;
    justify-content: center;
   align-items: center;
 
  margin: 0;
   
}

form{
    width:40%;
    border:2px solid black;
    padding: 30px;
   
}

label{
    width:30%;
}
input{
    padding:0.6rem;
    margin:0.3rem;
    width:70%;
}

.field{
    display:flex;
    justify-content: space-around;
    align-items: center;
    gap:0.2rem;
}